import { Component } from '@angular/core';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  Pin: String ="";
  ShowPin: Boolean = false;

  eventCapture(event) {
    this.ShowPin = false;
    this.Pin=event;
  }

  showPin() {
    this.ShowPin = !this.ShowPin;
  }
}



